.kendo__events{
    right:0;
}
.kendo__events-text{
    text-align: center;
    font-family: var(--font-events);
    color: white;
    font-size: 3.25rem;
}

.kendo__events-text-title{
    padding-top: 10rem;
}
.kendo__events-text-sub{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.kendo__events-text-time{
    display: flex;
    flex-direction: column;
    margin: 3rem 2rem;
}

.section__padding-events{
    margin-top: 0.5rem;
    scroll-margin-top: 6.4rem;
}
.parallax__events{
    /* The image used */
    background-image: url('../../assets/suzuki.png');

    /* Set a specific height */
    min-width: 100vw;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: 45% 60%;
    background-repeat: no-repeat;
    background-size: cover;
}

@media screen and (max-width: 550px){
    .kendo__events-text-sub{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .kendo__events-text-time{
        display: flex;
        flex-direction: column;
        margin: 3rem 2rem;
    }
  }