.gradient__bg{

   /* ff 3.6+ */
   background:-moz-radial-gradient(circle at 0% 0%, rgba(0, 127, 208, 1) 0%, rgba(0, 100, 164, 1) 100%);

   /* safari 5.1+,chrome 10+ */
   background:-webkit-radial-gradient(circle at 0% 0%, rgba(0, 127, 208, 1) 0%, rgba(0, 100, 164, 1) 100%);

   /* opera 11.10+ */
   background:-o-radial-gradient(circle at 0% 0%, rgba(0, 127, 208, 1) 0%, rgba(0, 100, 164, 1) 100%);

   /* ie 10+ */
   background:-ms-radial-gradient(circle at 0% 0%, rgba(0, 127, 208, 1) 0%, rgba(0, 100, 164, 1) 100%);

   /* global 92%+ browsers support */
   background:radial-gradient(circle at 0% 0%, rgba(0, 127, 208, 1) 0%, rgba(0, 100, 164, 1) 100%);
  
  }
  
.kendo__navbar{
    position:fixed;
    width:100vw;
    top:0;
    display:flex;
    justify-content:space-between;
    align-items:center;
    padding:0rem 1rem;
    z-index:11;
}

.kendo__navbar-links{
    flex: 1;
    display:flex;
    justify-content:flex-end;
    align-items:center;
    padding-bottom:1rem;
}

.kendo__navbar-logo img{
    display:flex;
    width:5rem;
    height:5rem;
    margin-top:0.5rem;
    margin-bottom:0.5rem;
}

.kendo__navbar-links_container{
    display: flex;
    flex-direction: row;
}

.kendo__navbar-links_container p{
    color:#fff;
    font-family:var(--font-family);
    font-weight:500;
    font-size:1.5rem;
    line-height:0px;
    text-transform:capitalize;
    margin-right:1rem;
    margin-top:1rem;
    cursor:pointer;
}
.kendo__navbar-menu{
    margin-left:20rem;
    display:none;
    position:relative;
    align-self:center;
    padding-top:1rem;
    right: 2vw;
}

.kendo__navbar-menu svg{
    cursor:pointer;
}

.kendo__navbar-menu_container{
    display:flex;
    align-content:flex-end;
    background:#c2a16c;
    position:fixed;
    top:6rem;
    right: 2vw;
    padding-right:2.5rem;
    padding-left:2.5rem;
    border-radius:.25rem;
}

.kendo__navbar-menu_container p{
    background:#c2a16c;
    font-size:1.5rem;
    font-family:var(--font-family);
    color:#fff;
    margin:1rem 0;
    width:100%;
    align-self:center;
}

.kendo__navbar-menu_container-links-sign{
    display:none;
}
@media screen and (max-width: 1050px){
    .kendo__navbar-links_container{
        display:none;
    }

    .kendo__navbar-menu{
        display:flex;
    }
}
@media screen and (max-width: 700px){
    .kendo__navbar-menu{
        right: 20vw;
    }
  }