.kendo__about{
    background-color:green;
    padding:0;
    margin:0;
    right:0;
}
.kendo__about-text{
    text-align:center;
    font-family:var(--font-about);
    color:white;
    font-size:3.25rem;
}

.kendo__about-text-title{
    padding-top:10rem;
}

.section__padding-about{
    margin-top: 0.5rem;
    scroll-margin-top: 6.4rem;
}
.parallax__about{
    /* The image used */
    background-image: url('../../assets/kendogamer.png');
    right:0;
    /* Set a specific height */
    min-height: 100vh;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
}